<template>
	<div class="wrap">
		<TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
		<TheOrderList :message='filtermessage'></TheOrderList>
		<ThePaginationBar :page="page" :total="total" :limit="limit"></ThePaginationBar>
	</div>
</template>

<script>
import TheOrderList from '@/components/ThePersonCenter/TheOrderList'
import TheCompiler from '@/components/ThePersonCenter/TheCompiler'
import ThePaginationBar from '@/components/ThePaginationBar.vue'
export default {

  data () {
    return {
      tabs: [
        {
          tabtxt: '未完成',
          active: false,
		  status: [0,6,7,8]
        },
        {
          tabtxt: '保障中',
          active: true,
		  status: 4
        },
        {
          tabtxt: '全部',
          active: false,
		  status: -1
        }
      ],
		message: [],
		page: 1,
		total: 0,
		limit: 10
    }
  },
  methods: {
    handleChangeTab (item) {
		if(!this.tabs.length)return
      this.tabs.find(tab => tab !== item && tab.active).active = false
      item.active = true
    }
  },
	computed: {
		filtermessage(){
			let tab = this.tabs.find(tab=>tab.active);
			let status = tab ? tab.status : 0;
			if(status == -1){
				return this.message;
			}else{
				return this.message.filter(item=>{
					if( typeof status == 'object' && status !== null ) {
						return status.findIndex(i=>i==item.status)>=0;
					}else{
						return item.status==status;
					}
				})
			}
		}
	},
  components: {
    TheOrderList,
    TheCompiler,
	ThePaginationBar
  },
	created(){
		let message = []
		this.$api.personCenter.getIndexOrderList({
			page: 1,
			limit: 10,
			status: 0
		})
		.then((data)=>{
			console.log(data)
			let message;
			try{
				message = JSON.parse(JSON.stringify(data.data.dataGrid.data))
			}catch(e){
				//TODO handle the exception
				console.error(e);message = [];
			}
			this.message = message;
		})
	}
}
</script>

<style lang="scss" scoped>
.wrap{
	box-shadow: 0px 2px 4px 0 rgba(219,219,219,.35);
	.orderBox{
		border-top: 1px solid #F1F4F4;
	}
}
</style>
